.ArticeList {
  padding-top: 60px;
  article {
    margin-bottom: 50px;
    h3 {
      a {
        font-size: 25px;
        color: var(--textLink);
        font-weight: bold;
      }
    }
    small {
      color: var(--textTip);
      font-size: 12px;
    }
    p {
      font-size: 16px;
      color: --textNormal;
    }
  }
}

.ArticeDetail {
  padding-top: 30px;
  & > header {
    margin-bottom: 20px;
    h1 {
      color: var(--textLink);
    }
    small {
      color: var(--textTip);
      font-size: 12px;
    }
    p {
      font-size: 16px;
      color: --textNormal;
    }
  }
}
