.LightAndDrakSwitch {
  touch-action: pan-x;
  display: inline-block;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;
  user-select: none;

  .content {
    width: 50px;
    height: 24px;
    padding: 0;
    border-radius: 30px;
    background-color: #0f1114;
    transition: all 0.2s ease;
    .content-a,
    .content-b {
      position: absolute;
      width: 17px;
      height: 17px;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      line-height: 0;
      transition: opacity 0.25s ease;
    }
    .content-a {
      left: 5px;
      opacity: 0;
      transition: opacity 0.25s ease;
    }
    .content-b {
      right: 5px;
      line-height: 0;
    }
  }
  .control {
    position: absolute;
    top: 1px;
    left: 1px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #fafafa;
    box-sizing: border-box;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transform: translateX(0);
  }
  &.LightAndDrakSwitch--checked {
    .content-a {
      opacity: 1;
    }
    .content-b {
      opacity: 0;
    }
    .control {
      transform: translateX(26px);
    }
  }
}
