.MdContent {
  img {
    max-width: 100%;
  }

  h1,
  h2,
  h3,
  h4,
  h5 {
    color: var(--textTitle);
    margin: 20px 0;
  }

  p {
    font-size: 15px;
    margin: 0;
    padding: 0;
    line-height: 1.6;
    margin: 10px 0;
    color: var(--textNormal);
  }

  a {
    color: var(--textLink);
    margin: 0 2px;
    padding: 0;
    vertical-align: baseline;
    text-decoration: none;
    box-shadow: 0 1px 0 0 currentColor;
  }
  a:hover {
    box-shadow: none;
  }

  ul,
  ol {
    padding: 0;
    padding-left: 18px;
    margin: 10px 0;
    li {
      margin: 5px 0;
    }
  }

  code {
    display: inline-block;
    margin: 2px 5px;
    padding: 0px 4px;
    color: var(--textLink);
    background: var(--bg-secondary);
    border-radius: 3px;
  }

  pre {
    border-radius: 3px;
    line-height: 1.7em;
    overflow: auto;
    padding: 10px 15px;
    background-color: var(--bg-secondary);
    box-shadow: var(--form-shadow);
    border-radius: 5px;
    code {
      border: 0;
      display: inline;
      max-width: initial;
      padding: 0;
      margin: 0;
      overflow: initial;
      line-height: 1.6em;
      font-size: 0.95em;
      white-space: pre;
      background: 0 0;
      color: var(--textNormal);
    }
  }

  aside {
    display: block;
    float: right;
    width: 390px;
  }
  blockquote {
    border-left: 0.5em solid var(--bg-secondary);
    padding: 0 0 0 2em;
    margin-left: 0;
    cite {
      line-height: 20px;
      color: var(--textNormal);
    }
    cite:before {
      content: '\2014 \00A0';
    }
    p {
      color: var(--textNormal);
    }
  }

  hr {
    text-align: left;
    color: var(--textNormal);
    height: 2px;
    padding: 0;
    margin: 16px 0;
    background-color: var(--bg-secondary);
    border: 0 none;
  }

  dl {
    padding: 0;
    dt {
      padding: 10px 0;
      margin-top: 16px;
      font-size: 1em;
      font-style: italic;
      font-weight: bold;
    }
    dd {
      padding: 0 16px;
      margin-bottom: 16px;
      margin-left: 0;
    }
  }

  table {
    border-spacing: 0;
    width: 100%;
    box-shadow: var(--form-shadow);
    border-radius: 5px;
    border: 1px solid var(--bg-secondary);
    th {
      background: var(--bg-secondary);
      padding: 10px;
      text-align: left;
      border-right: 1px solid var(--bg);
      padding: 13px 10px;
    }
    td {
      padding: 10px;
      text-align: left;
      border-top: 1px solid var(--bg-secondary);
      border-right: 1px solid var(--bg-secondary);
    }
    tr {
      td,
      th {
        &:last-child {
          border-right: none;
        }
      }
    }
  }

  table td:first-child,
  table th:first-child {
    border-left: none;
  }
}
