@import './globle.scss';
@import './pages/artice.scss';
@import './md/index.scss';
@import './component/toggle.scss';

img {
  width: 100%;
  vertical-align: bottom;
}

.App {
  max-width: 675px;
  width: 100%;
  padding: 50px 20px;
  margin: auto;
  color: var(--textNormal);
  .App-header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    .name {
      color: var(--textTitle);
      font-size: 20px;
      font-weight: bold;
    }
    i {
      font-style: normal;
      font-size: 20px;
      margin-right: 5px;
    }
  }
}

body {
  min-height: 100vh;
  background: var(--bg);
  color: var(--textNormal);
}

body {
  --bg: #fff;
  --bg-secondary: #f4f4f4;
  --textNormal: #222;
  --textTitle: #555;
  --textTip: #999;
  --textLink: #cd8073;
  --hr: rgba(0, 0, 0, 0.2);
  --inlineCode-bg: rgba(255, 229, 100, 0.2);
  --inlineCode-text: #1a1a1a;
  --form-shadow: 2px 2px 7px 0 rgba(26, 26, 27, 0.637);
}

body[data-theme='dark'] {
  -webkit-font-smoothing: antialiased;
  --bg: #282c35;
  --bg-secondary: #363c48;
  --textNormal: hsla(0, 0%, 100%, 0.8);
  --textTip: #999;
  --textTitle: #fff;
  --textLink: #cd8073;
  --hr: hsla(0, 0%, 100%, 0.2);
  --inlineCode-bg: rgba(115, 124, 153, 0.2);
  --inlineCode-text: #e6e6e6;
  --form-shadow: 3px 3px 3px 0 rgba(210, 214, 220, 0.5);
}
